<template>
  <div id="area">
    <div class="text-center">
      <h2
        class="title"
        v-bind:style="fontSize"
      >
        AGRUPACIÓN DE MOVIMIENTOS
      </h2>
    </div>
    <apexchart
      type="bar"
      height="350"
      :options="chartOptions"
      :series="series"
    ></apexchart>
  </div>
</template>

<style scoped>
.title {
  color: #13314a !important;
}
</style>

<script>
import VueApexCharts from "vue-apexcharts";
import { mapState, mapMutations } from "vuex";
import {
  eachDayOfInterval,
  eachWeekOfInterval,
  eachMonthOfInterval,
  add,
  lastDayOfMonth
} from "date-fns";
export default {
  name: "Column",
  components: {
    apexchart: VueApexCharts,
  },
  props: {
    initialDate: String,
    finalDate: String,
  },
  created() {
    this.movs = [...this.uniqueMovements];
    this.filteredMovs = [...this.uniqueMovements];
    this.calcInterval();
  },
  watch: {
    initialDate: function () {
      this.calcInterval();
    },
    finalDate: function () {
      this.calcInterval();
    },
  },
  computed: {
    fontSize(){
      return {
        fontSize: this.fontSizeTitle + " !important"
      }
    },
    movements() {
      return this.filteredMovs.sort(function (a, b) {
        var dateA = new Date(a.date);
        var dateB = new Date(b.date);
        if (dateA > dateB) {
          return 1;
        }
        if (dateA < dateB) {
          return -1;
        }
        return 0;
      });
    },
    series() {
      var series = [
        { name: "Ocio", data: [] },
        { name: "Servicios", data: [] },
        { name: "Compras", data: [] },
        { name: "Otro", data: [] },
      ];

      var indexDates = 0;
      var indexMovements = 0;

      this.times.forEach((timeframe) => {
        let d = new Date(timeframe);
        let dNext = new Date(this.times[indexDates + 1]);
        let dMov;
        if (indexMovements < this.movements.length) {
          dMov = new Date(this.movements[indexMovements].date);
        } else {
          dMov = new Date(this.movements[this.movements.length - 1].date);
        }
        series[0].data.push(0);
        series[1].data.push(0);
        series[2].data.push(0);
        series[3].data.push(0);
        if (d.getDate() == 26) {
          let aux = this.movements[indexMovements];
        }
        while (
          indexMovements < this.movements.length &&
          d.getTime() <= dMov.getTime()
        ) {
          let mov = this.movements[indexMovements];

          if (
            (dNext != "Invalid Date" && dMov.getTime() < dNext.getTime()) ||
            dNext == "Invalid Date"
          ) {
            if (mov.category === "Ocio") {
              series[0].data[indexDates] += mov.amount;
            } else if (mov.category === "Servicios") {
              series[1].data[indexDates] += mov.amount;
            } else if (mov.category === "Compras") {
              series[2].data[indexDates] += mov.amount;
            } else if (mov.category === "Otro") {
              series[3].data[indexDates] += mov.amount;
            }
            indexMovements++;

            if (indexMovements < this.movements.length) {
              dMov = new Date(this.movements[indexMovements].date);
            }
          } else {
            break;
          }
        }
        indexDates++;
      });

      return series;
    },
    chartOptions() {
      var chartOptions = {
        chart: {
          type: "bar",
          height: 350,
          stacked: true,
          toolbar: {
            show: true,
          },
          zoom: {
            enabled: true,
          },
        },
        colors: [this.colOcio, this.colServicios, this.colCompras, this.colOtro],
        responsive: [
          {
            breakpoint: 480,
            options: {
              legend: {
                position: "bottom",
                offsetX: -10,
                offsetY: 0,
              },
            },
          },
        ],
        plotOptions: {
          bar: {
            horizontal: false,
          },
        },
        xaxis: {
          type: "datetime",
          categories: this.times,
        },
        legend: {
          position: "bottom",
          offsetY: 7,
        },
        fill: {
          opacity: 1,
        },
        tooltip: {
          x: {
            format: "dd/MM/yy",
          },
        }
      };
      return chartOptions;
    },
    filters() {
      var filters = {
        initialDate: null,
        finalDate: null,
        type: "Gasto",
      };
      if (this.interval == "Day") {
        filters.initialDate = this.initialDate;
        filters.finalDate = this.finalDate;
      } else if (this.interval == "Week") {
        filters.initialDate = this.times[0];
        let lastMonday = this.times[this.times.length - 1];
        var result = add(new Date(lastMonday), {
          days: 6,
        });
        let date = result.toLocaleDateString();
        
        let elements = date.split("/");
        let reversed = elements.reverse();
        filters.finalDate = reversed.join("-")
      } else {
        filters.initialDate = this.times[0];
        let lastMonth = this.times[this.times.length - 1];
        var result = lastDayOfMonth(new Date(lastMonth))
        let date = result.toLocaleDateString();
        
        let elements = date.split("/");
        let reversed = elements.reverse();
        filters.finalDate = reversed.join("-")
      }
      return filters;
    },
    ...mapState(["uniqueMovements", "colOcio", "colServicios", "colCompras", "colOtro", "fontSizeTitle"]),
  },
  data() {
    return {
      interval: "Day",
      filteredMovs: [],
      movs: [],
      times: [],
    };
  },
  methods: {
    calcInterval() {
      var diffInDays =
        (new Date(this.finalDate).getTime() -
          new Date(this.initialDate).getTime()) /
        (1000 * 3600 * 24);
      if (diffInDays <= 31) {
        this.interval = "Day";
        this.fillDays();
      } else if (diffInDays < 90) {
        this.interval = "Week";
        this.fillWeeks();
      } else {
        this.interval = "Month";
        this.fillMonths()
      }
    },
    fillDays() {
      this.times = [];
      var days = eachDayOfInterval(
        {
          start: new Date(this.initialDate),
          end: new Date(this.finalDate),
        }
        //{ weekStartsOn: 1 }
      );
      days.forEach((element) => {
        var date = new Date(element);
        date = date.toLocaleDateString();
        let elements = date.split("/");
        let reversed = elements.reverse();
        this.times.push(reversed.join("-"));
      });
      this.filterMovs();
    },
    fillWeeks() {
      this.times = [];
      var weeks = eachWeekOfInterval(
        {
          start: new Date(this.initialDate),
          end: new Date(this.finalDate),
        },
        { weekStartsOn: 1 }
      );
      weeks.forEach((element) => {
        var date = new Date(element);
        date = date.toLocaleDateString();
        let elements = date.split("/");
        let reversed = elements.reverse();
        this.times.push(reversed.join("-"));
      });
      this.filterMovs();
    },
    fillMonths(){
      this.times = [];
      var months = eachMonthOfInterval(
        {
          start: new Date(this.initialDate),
          end: new Date(this.finalDate),
        }
      );
      months.forEach((element) => {
        var date = new Date(element);
        date = date.toLocaleDateString();
        let elements = date.split("/");
        let reversed = elements.reverse();
        let aux = new Date(reversed.join("-"));
        this.times.push(reversed.join("-"));
      });
      this.filterMovs();
    },
    filterMovs() {
      const cf = new this.$MultiFilters(this.movs, this.filters);
      var items = this.movs;
      cf.registerFilter("initialDate", function (initialDate, items) {
        if (initialDate === "" || initialDate === null) return items;
        var initialDateForm = new Date(initialDate);
        return items.filter((item) => {
          let itemDate = new Date(item.date);
          return itemDate >= initialDateForm;
        }, initialDate);
      });
      cf.registerFilter("finalDate", function (finalDate, items) {
        if (finalDate === "" || finalDate === null) return items;
        var finalDateForm = new Date(finalDate);
        return items.filter((item) => {
          let itemDate = new Date(item.date);
          return itemDate <= finalDateForm;
        }, finalDate);
      });
      cf.registerFilter("type", function (searchWord, items) {
        if (searchWord.trim() === "") return items;
        return items.filter((item) => {
          return item.type.toLowerCase().includes(searchWord.toLowerCase());
        }, searchWord);
      });
      this.filteredMovs = cf.runFilters();
    },
  },
};
</script>
