<template>
  <div id="multiple">
    <div class="text-center">
      <h2
        class="title"
        v-bind:style="fontSize"
      >
        EVOLUCIÓN DE GASTOS POR CATEGORÍA
      </h2>
    </div>
    <apexchart
      type="line"
      height="320"
      :options="chartOptions"
      :series="series"
    ></apexchart>
  </div>
</template>

<style scoped>
.title {
  color: #13314a !important;
}
</style>

<script>
import VueApexCharts from "vue-apexcharts";
import { mapState, mapMutations } from "vuex";

export default {
  name: "Multiple",
  components: {
    apexchart: VueApexCharts,
  },
  props: {
    movs: {
      type: Array,
      required: true,
    },
    initialDate: String,
    finalDate: String,
  },
  mounted() {
    this.movements.sort(function(a, b) {
      var dateA = new Date(a.date);
      var dateB = new Date(b.date);
      if (dateA > dateB) {
        return 1;
      }
      if (dateA < dateB) {
        return -1;
      }
      return 0;
    });
  },
  computed: {
    fontSize(){
      return {
        fontSize: this.fontSizeTitle + " !important"
      }
    },
    movements() {
      return this.movs.sort(function(a, b) {
        var dateA = new Date(a.date);
        var dateB = new Date(b.date);
        if (dateA > dateB) {
          return 1;
        }
        if (dateA < dateB) {
          return -1;
        }
        return 0;
      });
    },
    dates() {
      if (this.movements === undefined || this.movements.length === 0)
        return [];
      var dates = [];
      var initial, final;
      if (
        this.initialDate === "" ||
        this.initialDate === undefined ||
        this.initialDate === null
      ) {
        initial = new Date(this.movements[0].date);
      } else {
        initial = new Date(this.initialDate);
      }
      if (
        this.finalDate === "" ||
        this.finalDate === undefined ||
        this.finalDate === null
      ) {
        final = new Date(this.movements[this.movements.length - 1].date);
      } else {
        final = new Date(this.finalDate);
      }
      for (initial; initial <= final; initial.setDate(initial.getDate() + 1)) {
        dates.push(new Date(initial).toISOString().substr(0, 10));
      }
      if (!dates.includes(final.toISOString().substr(0, 10))) {
        dates.push(final.toISOString().substr(0, 10));
      }
      return dates;
    },
    series() {
      var totalDates = [...this.dates];
      if (totalDates.length === 0) {
        return [];
      }
      var series = [
        {
          name: "Ocio",
          data: [],
        },
        {
          name: "Servicios",
          data: [],
        },
        {
          name: "Compras",
          data: [],
        },
        {
          name: "Otro",
          data: [],
        },
      ];
      var balOcio = 0,
        balServ = 0,
        balCom = 0,
        balOtro = 0;
      var indexDates = 0;
      var indexMovements = 0;
      totalDates.forEach((date) => {
        series[0].data.push(balOcio);
        series[1].data.push(balServ);
        series[2].data.push(balCom);
        series[3].data.push(balOtro);
        let d = new Date(date);
        let dMov;
        if (indexMovements < this.movements.length) {
          dMov = new Date(this.movements[indexMovements].date);
        } else {
          dMov = new Date(this.movements[this.movements.length - 1].date);
        }
        while (
          indexMovements < this.movements.length &&
          d.getTime() === dMov.getTime()
        ) {
          let mov = this.movements[indexMovements];
          if (mov.type === "Gasto") {
            if (mov.category === "Ocio") {
              series[0].data[indexDates] += mov.amount;

              balOcio += mov.amount;
            } else if (mov.category === "Servicios") {
              series[1].data[indexDates] += mov.amount;
              balServ += mov.amount;
            } else if (mov.category === "Compras") {
              series[2].data[indexDates] += mov.amount;
              balCom += mov.amount;
            } else if (mov.category === "Otro") {
              series[3].data[indexDates] += mov.amount;
              balOtro += mov.amount;
            }
          }
          indexMovements++;
          if (indexMovements < this.movements.length) {
            dMov = new Date(this.movements[indexMovements].date);
          }
        }
        indexDates++;
      });
      return series;
    },
    chartOptions() {
      var chartOptions = {
        chart: {
          height: 350,
          type: "line",
          toolbar: {
            show:true,
          },
          zoom: {
            enabled: true,
          },
        },
        colors: [this.colOcio, this.colServicios, this.colCompras, this.colOtro],
        dataLabels: {
          enabled: false,
        },
        stroke: {
          width: [5, 5, 5, 5],
          curve: "straight",
        },
        legend: {
          offsetY: 7,
          tooltipHoverFormatter: function(val, opts) {
            return (
              val +
              " - " +
              opts.w.globals.series[opts.seriesIndex][opts.dataPointIndex] +
              ""
            );
          },
        },
        markers: {
          size: 0,
          hover: {
            sizeOffset: 6,
          },
        },
        xaxis: {
          categories: this.dates,
          type: "datetime",
        },
        tooltip: {
          x: {
            format: "dd/MM/yy",
          },
          y: [
            {
              title: {
                formatter: function(val) {
                  return val;
                },
              },
            },
            {
              title: {
                formatter: function(val) {
                  return val;
                },
              },
            },
            {
              title: {
                formatter: function(val) {
                  return val;
                },
              },
            },
          ],
        },
        grid: {
          borderColor: "#f1f1f1",
        },
      };
      return chartOptions;
    },
    ...mapState(["colOcio", "colServicios", "colCompras", "colOtro", "fontSizeTitle"]),
  },
  data() {
    return {};
  },
};
</script>
