<template>
  <v-container
  fluid
    >
  
      <v-row justify="center" align="center" style="margin-top: 5px; margin-right: 15px; margin-left:15px; margin-bottom: 5px">
        <v-menu
          v-model="menu1"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="290px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="initialDate"
              label="Fecha de inicio"
              readonly
              v-bind="attrs"
              v-on="on"
              class="tf"
            ></v-text-field>
          </template>
          <v-date-picker
            v-model="initialDate"
            @input="filterInitialDate"
            :landscape="true"
          ></v-date-picker>
        </v-menu>
        <v-menu
          v-model="menu"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="290px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="finalDate"
              label="Fecha de fin"
              readonly
              :error-messages="finalDateErrors"
              v-bind="attrs"
              v-on="on"
              class="tf"
            ></v-text-field>
          </template>
          <v-date-picker
            v-model="finalDate"
            @input="
              $v.finalDate.$touch();
              filterFinalDate();
            "
            @blur="$v.finalDate.$touch()"
            :landscape="true"
          ></v-date-picker>
        </v-menu>
        <div style="margin-top: -15px">
          <v-btn large elevation="2" @click="clear" color="primary"
            >Limpiar</v-btn
          ></div>
      </v-row>
    
    <div
      class="elevation-20"
      style="border: 3px solid #13314a; border-radius: 20px; padding-right: 20px; padding-left: 20px; margin-bottom: 20px; margin-right: 30px; margin-left: 30px"
    >
      <v-row justify="center" align="center" width="90%">
        <v-col sm="12" md="6">
          <Balance
            style="z-index: 1"
            :movements="totalMovs"
            :initialDate="initialDate"
            :finalDate="finalDate"
            :link="false"
          />
        </v-col>
        <v-col sm="12" md="6" class="text-center d-flex justify-space-around">
          <div>
            <Donut :movements="movs" :link="false" />
          </div>
        </v-col>
      </v-row>
    </div>
    <div
      class="elevation-20"
      style="border: 3px solid #13314a; border-radius: 20px; padding-right: 20px; padding-left: 20px; margin-bottom: 20px; margin-right: 30px; margin-left: 30px"
    >
      <v-row class="mt-1.5">
        <v-col sm="12" md="6"
          ><Multiple
            :movs="movs"
            :initialDate="initialDate"
            :finalDate="finalDate"
        /></v-col>
        <v-col sm="12" md="6"
          ><Column :initialDate="initialDateColumn" :finalDate="finalDateColumn" style="z-index:1"
        /></v-col>
      </v-row>
    </div>
    <v-btn
      fab
      dark
      large
      color="primary"
      fixed
      right
      bottom
      class="mb-14"
      style="font-size:2em;z-index: 5"
      @click="dialog = true"
      >?</v-btn
    >

    <Help title="GRÁFICOS" v-model="dialog" />
  </v-container>
</template>

<style>
.tf {
  max-width: 500px;
  margin-right: 40px;
}
</style>

<script>
import Balance from "../components/Charts/Balance";
import Pie from "../components/Charts/Pie";
import Donut from "../components/Charts/Donut";
import Column from "../components/Charts/Column";
import Multiple from "../components/Charts/Multiple";
import Help from "../components/Help";
import VueApexCharts from "vue-apexcharts";

import { mapState, mapMutations } from "vuex";
import { validationMixin } from "vuelidate";
import {
  required,
  maxLength,
  minValue,
  minLength,
  email,
  alphaNum,
  decimal,
  requiredIf,
  numeric,
  integer,
} from "vuelidate/lib/validators";
export default {
  name: "Charts",
  mixins: [validationMixin],
  validations: {
    finalDate: {
      isAfter: function(date) {
        if (
          this.initialDate === "" ||
          this.initialDate === null ||
          this.finalDate === "" ||
          this.finalDate === null
        )
          return true;
        var initial = new Date(this.initialDate);
        var final = new Date(date);
        return final > initial;
      },
    },
  },
  components: {
    VueApexCharts,
    Balance,
    Pie,
    Column,
    Multiple,
    Donut,
    Help,
  },
  computed: {
    finalDateErrors() {
      const errors = [];
      if (!this.$v.finalDate.$dirty) return errors;
      !this.$v.finalDate.isAfter &&
        errors.push("La fecha de fin debe ser posterior a la de inicio.");
      return errors;
    },
    initialDateColumn() {
      if (
        this.initialDate === "" ||
        this.initialDate == undefined ||
        this.initialDate == null
      ) {
        let aux = [...this.uniqueMovements];
        aux.sort(function(a, b) {
          var dateA = new Date(a.date);
          var dateB = new Date(b.date);
          if (dateA > dateB) {
            return 1;
          }
          if (dateA < dateB) {
            return -1;
          }
          return 0;
        });
        return aux[0].date;
      } else {
        return this.initialDate;
      }
    },
    finalDateColumn() {
      if (
        this.finalDate === "" ||
        this.finalDate == undefined ||
        this.finalDate == null
      ) {
        let aux = [...this.uniqueMovements];
        aux.sort(function(a, b) {
          var dateA = new Date(a.date);
          var dateB = new Date(b.date);
          if (dateA > dateB) {
            return 1;
          }
          if (dateA < dateB) {
            return -1;
          }
          return 0;
        });
        return aux[aux.length - 1].date;
      } else {
        return this.finalDate;
      }
    },
    ...mapState(["uniqueMovements"]),
  },
  mounted() {
    // this.initialDate = new Date();
    // this.initialDate.setDate(new Date(this.finalDate).getDate() - 30);
    // this.initialDate = this.initialDate.toISOString().substr(0, 10);
    // this.filterInitialDate();
    // this.filterFinalDate();
    // this.filter();
  },
  created() {
    this.movs = [...this.uniqueMovements];
    this.totalMovs = this.movs;
    this.finalDate = new Date().toISOString().substr(0, 10);
    this.initialDate = new Date(
      new Date().getFullYear(),
      new Date().getMonth(),
      new Date().getDate() - 30
    )
      .toISOString()
      .substr(0, 10);
    this.filterStart();
  },
  data() {
    return {
      movs: [],
      totalMovs: [],
      menu1: false,
      menu: false,
      initialDate: "",
      finalDate: "",
      filters: {
        initialDate: "",
        finalDate: "",
      },
      dialog: false,
    };
  },
  methods: {
    filter() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        const cf = new this.$MultiFilters(this.uniqueMovements, this.filters);
        var items = [...this.uniqueMovements];
        cf.registerFilter("initialDate", function(initialDate, items) {
          if (initialDate === "" || initialDate === null) return items;
          var initialDateForm = new Date(initialDate);
          return items.filter((item) => {
            let itemDate = new Date(item.date);
            return itemDate >= initialDateForm;
          }, initialDate);
        });
        cf.registerFilter("finalDate", function(finalDate, items) {
          if (finalDate === "" || finalDate === null) return items;
          var finalDateForm = new Date(finalDate);
          return items.filter((item) => {
            let itemDate = new Date(item.date);
            return itemDate <= finalDateForm;
          }, finalDate);
        });
        this.movs = cf.runFilters();
      }
    },
    filterInitialDate() {
      this.menu1 = false;
      this.filters = this.$MultiFilters.updateFilters(this.filters, {
        initialDate: this.initialDate,
      });
      this.filter();
    },
    filterFinalDate() {
      this.menu = false;
      this.filters = this.$MultiFilters.updateFilters(this.filters, {
        finalDate: this.finalDate,
      });
      this.filter();
    },
    filterStart() {
      this.filters = this.$MultiFilters.updateFilters(this.filters, {
        initialDate: this.initialDate,
      });
      this.filters = this.$MultiFilters.updateFilters(this.filters, {
        finalDate: this.finalDate,
      });
      this.filter();
    },
    clear() {
      this.$v.$reset();
      this.initialDate = "";
      this.finalDate = "";
      this.movs = [...this.uniqueMovements];
      this.filters = {
        initialDate: "",
        finalDate: "",
      };
    },
  },
};
</script>
